export default {
	data() {
		return {
			list:[],
			topic_id:'',
			pagemax:'',//总的数量
			pagenow:0,//当前的页数
			shownumber:10,//每页的分页条数
			keywords:'',
			
			contentText: {
				contentdown: "查看更多",
				contentrefresh: "加载中",
				contentnomore: "没有更多"
			},
			status:'loading',
			shouLoading:true,
			haveData:false,
		}
	},
	onLoad(e) {
		var t=this;	
		if(e.id){
			t.topic_id=e.id;
		}
		//获取新闻列表
		t.getList();
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.shownumber >= this.pagemax) {
			return;
		}
		setTimeout(() => {
			this.setDate();
		}, 100);
	},
	onPullDownRefresh() {
		this.initData();
	},	
	methods: {
		log_detail:function(id){
			uni.navigateTo({
				url: '../zhuanti/notes-detail?id='+id
			})
		},
		search:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				pagesize:999,
				page:0,
			}
			t.urlRequest.urlRequest(t.func.eduNotesList,data,function(res){
				for (var i = 0; i < res.data.list.length; i++) {
					res.data.list[i]['add_time']=res.data.list[i]['add_time'].substring(0,10)
				}
				var data=res.data.list;
				t.list=data;
				uni.hideLoading();
			})
		},
		getList:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.eduNotesList,data,function(res){
				t.pagemax=res.data.count;
				if(res.data.list){
					for (var i = 0; i < res.data.list.length; i++) {
						res.data.list[i]['add_time']=res.data.list[i]['add_time'].substring(0,10)
						t.list.push(res.data.list[i]);
					}
				}
				
				uni.hideLoading();
			})
			
		},
		initData(){
			uni.showLoading();
			const t = this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.eduNotesList,data,function(res){
				t.pagemax=res.data.count;
				for (var i = 0; i < res.data.list.length; i++) {
					
					res.data.list[i]['add_time']=res.data.list[i]['add_time'].substring(0,10)
				}
				t.list=res.data.list;
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		},
		setDate() {
			uni.showLoading();
			this.shownumber=this.pagenow*10;
			this.pagenow++;
			var t=this;
			
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.eduNotesList,data,function(res){
				if(res.data.list){
					setTimeout(() => {
						t.haveData =true
					}, 100);
					for (var i = 0; i < res.data.list.length; i++) {
						
						res.data.list[i]['add_time']=res.data.list[i]['add_time'].substring(0,10)
					}
					t.list = t.list.concat(res.data.list);
					t.pagemax=res.data.count
				}
				uni.hideLoading();
			})
		}
	}
}
